* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  min-height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}
h2 {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  letter-spacing: 1px;
}
.App::before {
  top: 50%;
  left: 20px;
  position: absolute;
  content: "PAWAN KUMAR";
  width: 5px;
  writing-mode: vertical-rl;
  text-orientation: upright;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  animation: blink 0.6s infinite ease-in-out alternate;
}
@keyframes blink {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.App {
  margin-top: 1rem;
  width: min(90%, 50rem);
  margin-inline: auto auto;
}
.profile {
  display: grid;
  place-items: center;
}
.profile img {
  display: block;
  width: 100%;
  max-width: 400px;
  height: 300px;
  object-fit: contain;
}
figcaption {
  text-align: center;
  padding-top: 1rem;
  letter-spacing: 1px;
}
.about {
  margin-top: 2rem;
  font-size: clamp(1.2rem, 2rem, 1rem);
  letter-spacing: 1px;
}

.about h2 {
  padding-bottom: 0.6rem;
}
.follow h2 {
  margin-top: 2rem;
  padding-bottom: 1rem;
}

.follow list {
  margin-top: 2rem;
}

.follow ul li {
  font-size: 1.2rem;
  padding-bottom: 0.6rem;
}
.follow ul li a {
  color: #8ab4f8;
  text-decoration: none;
}
.follow ul li a:hover {
  text-decoration: underline;
  transition: 0.2s text-decoration;
}

.download__link {
  position: absolute;
  left: 50%;
  right: 50%;
  text-transform: uppercase;
  color: #8ab4f8;
  text-decoration: none;
  margin: 4px 2px;
  width: 100px;
  height: 30px;
  width: 30px;
  background: #8ab4f8;
}
.hobbies h2 {
  margin-top: 2rem;
}
.hobbies ul {
  margin-top: 1rem;
}
.hobbies ul li {
  font-size: 1.2rem;
  padding-bottom: 0.6rem;
}

.app-spinner {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 80px;
  height: 80px;
}

.app-spinner div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #fff;
  animation: anim 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.app-spinner div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.app-spinner div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.app-spinner div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}
@keyframes anim {
  0% {
    top: 8px;
    height: 64px;
  }
  50%,
  100% {
    top: 24px;
    height: 32px;
  }
}
